<template>
	<view class="bigBox">
		<!-- 自定义标题 -->
		<uni-nav-bar fixed="true" color="#007AFF" background-color="#F8F8F8" :status-bar="true" title="事件中心" />
	   
		<!-- 顶部选项卡 -->
		
		<scroll-view id="nav-bar" class="nav-bar" scroll-x scroll-with-animation :scroll-left="scrollLeft">
			<view v-for="(item, index) in tabBars" :key="item.id" class="nav-item" :class="{ current: index === tabCurrentIndex }" :id="'tab' + index" @click="changeTab(index)">
				{{ item.name }}
			</view>
		</scroll-view>

		<!-- 下拉刷新组件 -->
		<mix-pulldown-refresh ref="mixPulldownRefresh" class="panel-content" :top="90" @refresh="onPulldownReresh" @setEnableScroll="setEnableScroll">
			<!-- 内容部分 -->
			<swiper id="swiper" class="swiper-box" :duration="300" :current="tabCurrentIndex" @change="changeTab">
				<swiper-item v-for="tabItem in tabBars" :key="tabItem.id">
					<scroll-view class="panel-scroll-box" :scroll-y="enableScroll" @scrolltolower="loadMore">
						<!-- 
							* 新闻列表 
							* 和nvue的区别只是需要把uni标签转为weex标签而已
							* class 和 style的绑定限制了一些语法，其他并没有不同
						-->
						<view v-for="(item, index) in tabItem.newsList" :key="index" class="news-item" @click="navToDetails(item)">
							<view class="items">
								<text class="item-title111 item-title20">
								<text class="item-title item-titless" >上报人:</text>	
								<text class="it1 it33" space="emsp" decode="true">{{ item.reporter}}</text>
								</text>
								<text class="item-title111" space="emsp" :decode="trues" @tap.stop="callPhone(item.phone)">
									电话:
									<text class="it2">{{ item.phone }}</text>
								</text>
								<text class="item-title111 last-cild" space="emsp">
									状态:
									<text class="it2" :class="{ statusd: item.status == 3||item.status == 4 }">{{ statusChange(item.status) }}</text>
								</text>
							</view>
							<view class="items">
								<text class="item-title" space="emsp">
									事发地点:
									<text class="it1">{{ item.address }}</text>
								</text>
							</view>
							<view class="items it4">
								<text class="item-title item-title1" space="emsp">上报内容:</text>
								<text class="it3">{{ item.content }}</text>
							</view>
							<view class="items">
								<text class="item-title" space="emsp">
									上报时间:
									<text class="it1">{{ item.time }}</text>
								</text>
							</view>
						</view>

						<!-- 上滑加载更多组件 -->
						<mix-load-more :status="tabItem.loadMoreStatus"></mix-load-more>
					</scroll-view>
				</swiper-item>
			</swiper>
		</mix-pulldown-refresh>
	</view>
</template>

<script>
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import json from '@/json';
import mixPulldownRefresh from '@/components/mix-pulldown-refresh/mix-pulldown-refresh';
import mixLoadMore from '@/components/mix-load-more/mix-load-more';
let windowWidth = 0,
	scrollTimer = false,
	tabBar;
export default {
	components: {
		mixPulldownRefresh,
		mixLoadMore,
		uniNavBar
	},
	data() {
		return {
			trues: true,
			tabCurrentIndex: 0, //当前选项卡索引
			scrollLeft: 0, //顶部选项卡左滑距离
			enableScroll: true,
			tabBars: [],
			status: 0,
			pageNo:1
		};
	},

	async onLoad() {
		// 获取屏幕宽度
		windowWidth = uni.getSystemInfoSync().windowWidth;
		this.loadTabbars();
	},
	onReady() {},
	methods: {
		ontrueGetList() {
			console.log('事件中心');
			// 获取屏幕宽度
			windowWidth = uni.getSystemInfoSync().windowWidth;
			this.loadTabbars();
		},
		//打电话
		callPhone(phone) {
			console.log(phone);
			uni.makePhoneCall({
				phoneNumber: phone //仅为示例
			});
		},
		statusChange(index) {
			switch (index) {
				case 0:
					return '待处理';
					break;
				case 1:
					return '待办';
					break;
				case 2:
					return '待办结';
					break;

				case 3:
					return '已办结';
					break;
					case 4:
						return '无效';
						break;
				default:
					return '待处理';
			}
		},
		/**
		 * 数据处理方法在vue和nvue中通用，可以直接用mixin混合
		 * 这里直接写的
		 * mixin使用方法看index.nuve
		 */
		//获取分类
		loadTabbars() {
			let tabList = json.tabList;
			tabList.forEach(item => {
				item.newsList = [];
				item.loadMoreStatus = 0; //加载更多 0加载前，1加载中，2没有更多了
				item.refreshing = 0;
			});
			this.tabBars = tabList;
			this.loadNewsList('add');
		},
		//新闻列表
		loadNewsList(type) {
			let tabItem = this.tabBars[this.tabCurrentIndex];

			//type add 加载更多 refresh下拉刷新
			if (type === 'add') {
				
				if (tabItem.loadMoreStatus === 2) {
					return;
				}
				tabItem.loadMoreStatus = 1;
			}
			// #ifdef APP-PLUS
			else if (type === 'refresh') {
				tabItem.refreshing = true;
				this.pageNo=1
			}
			// #endif
			
			var obj={type: this.tabCurrentIndex-1,pageNo:this.pageNo}
			if(this.tabCurrentIndex==0){
				delete obj.type 
			}
			this.$http
				.get('/interface/rest/http/xlwb/xlgc-wb-xcx-sjzx-sjlb.htm', {
					params:obj
					
				})
				.then(res => {
					console.log(res);
					if (res.data.msgState == 1) {
						var list = res.data.list;
						console.log('list');
						console.log(list);
						console.log("type");
						console.log(type);
						
						
						if (type === 'refresh') {
							tabItem.newsList = []; //刷新前清空数组
						}
						list.forEach(item => {
							
							tabItem.newsList.push(item);
						});
						this.pageNo+=1
						//下拉刷新 关闭刷新动画
						if (type === 'refresh') {
							this.$refs.mixPulldownRefresh && this.$refs.mixPulldownRefresh.endPulldownRefresh();
							// #ifdef APP-PLUS
							tabItem.refreshing = false;
							// #endif
							if(res.data.totalPages===0||res.data.curPage===res.data.totalPages){
							tabItem.loadMoreStatus =2	
							}else tabItem.loadMoreStatus = 0;
							
						}
						//上滑加载 处理状态
						if (type === 'add') {
							if(res.data.totalPages===0||res.data.curPage===res.data.totalPages){
							tabItem.loadMoreStatus =2;
								this.pageNo=1
							}
							}
						
					} else {
						uni.showToast({
							title: res.data.msg,
							duration: 2000,
							icon:"none"
						});
					}
				})
				.catch(err => {
					console.log(err);
				});
			
		},
		//事件详情
		navToDetails(item) {
			console.log(item)
			let data = {
				id: item.id,
				
			};
			console.log( item.id)
			let url = item.videoSrc ? 'videoDetails' : 'details';

			uni.navigateTo({
				url: `/pages/details/${url}?data=${JSON.stringify(data)}`
			});
		},

		//下拉刷新
		onPulldownReresh() {
			this.pageNo=1;
			this.loadNewsList('refresh');
			
		},
		//上滑加载
		loadMore() {
			this.loadNewsList('add');
		},
		//设置scroll-view是否允许滚动，在小程序里下拉刷新时避免列表可以滑动
		setEnableScroll(enable) {
			if (this.enableScroll !== enable) {
				this.enableScroll = enable;
			}
		},

		//tab切换
		async changeTab(e) {
			this.pageNo=1
			if (scrollTimer) {
				//多次切换只执行最后一次
				clearTimeout(scrollTimer);
				scrollTimer = false;
			}
			let index = e;
			//e=number为点击切换，e=object为swiper滑动切换
			if (typeof e === 'object') {
				index = e.detail.current;
			}
			if (typeof tabBar !== 'object') {
				tabBar = await this.getElSize('nav-bar');
			}
			//计算宽度相关
			let tabBarScrollLeft = tabBar.scrollLeft;
			let width = 0;
			let nowWidth = 0;
			//获取可滑动总宽度
			for (let i = 0; i <= index; i++) {
				let result = await this.getElSize('tab' + i);
				width += result.width;
				if (i === index) {
					nowWidth = result.width;
				}
			}
			if (typeof e === 'number') {
				//点击切换时先切换再滚动tabbar，避免同时切换视觉错位
				this.tabCurrentIndex = index;
			}
			//延迟300ms,等待swiper动画结束再修改tabbar
			scrollTimer = setTimeout(() => {
				if (width - nowWidth / 2 > windowWidth / 2) {
					//如果当前项越过中心点，将其放在屏幕中心
					this.scrollLeft = width - nowWidth / 2 - windowWidth / 2;
				} else {
					this.scrollLeft = 0;
				}
				if (typeof e === 'object') {
					this.tabCurrentIndex = index;
				}
				this.tabCurrentIndex = index;

				//第一次切换tab，动画结束后需要加载数据
				let tabItem = this.tabBars[this.tabCurrentIndex];
				console.log('111111');
				console.log(this.tabCurrentIndex);
				console.log(tabItem.loaded);
				//重新获取新数据
				this.loadNewsList('add');
				// if (this.tabCurrentIndex !== 0 && tabItem.loaded !== true) {
				// 	this.loadNewsList('add');
				// 	tabItem.loaded = true;
				// }
			}, 300);
		},
		//获得元素的size
		getElSize(id) {
			return new Promise((res, rej) => {
				let el = uni
					.createSelectorQuery()
					.in(this)
					.select('#' + id);
				el.fields(
					{
						size: true,
						scrollOffset: true,
						rect: true
					},
					data => {
						res(data);
					}
				).exec();
			});
		}
	}
};
</script>

<style lang="scss">

.bigBox {
	
	page,
	.content {
		background-color: #f8f8f8;
		height: 100%;
		overflow: hidden;
	}

	/* 顶部tabbar */
	.nav-bar {
		position: fixed;
		z-index: 10;
		width: 750rpx;
		height: 90rpx;
		
		white-space: nowrap;
		box-shadow: 0 2upx 8upx rgba(0, 0, 0, 0.06);
		background-color: #fff;
		.nav-item {
			display: inline-block;
			width: 33.3%;
			height: 90rpx;
			text-align: center;
			line-height: 90rpx;
			font-size: 30upx;
			color: #303133;
			position: relative;
			&:after {
				content: '';
				width: 0;
				height: 0;
				border-bottom: 4upx solid #007aff;
				position: absolute;
				left: 50%;
				bottom: 0;
				transform: translateX(-50%);
				transition: 0.3s;
			}
		}
		.current {
			color: #007aff;
			&:after {
				width: 50%;
			}
		}
	}

	.swiper-box {
		height: 100%;
		margin-top: 90rpx;
	}

	.panel-scroll-box {
		height: 100%;

		.panel-item {
			background: #fff;
			padding: 30px 0;
			border-bottom: 2px solid #000;
		}
	}

	/**
	 * 新闻列表 直接拿的nvue样式修改，,
	 * 一共需要修改不到10行代码, 另外px需要直接修改为upx，只有单位不一样，计算都是一样的
	 * 吐槽：难道不能在编译的时候把nuve中的upx转为px? 这样就不用修改单位了
	 */
	.video-wrapper {
		width: 100%;
		height: 440upx;
		.video {
			width: 100%;
		}
	}

	.img {
		width: 100%;
		height: 100%;
	}
	.news-item {
		position: relative;
	}
	/* 修改结束 */

	/* 新闻列表  emmm 仅供参考 */
	.news-item {
		width: 710rpx;
		padding: 20rpx 20rpx 1rpx 20rpx;
		background-color: #fff;
		margin-bottom: 20rpx;
	}
	.item-title{
		font-size: 28rpx;
		color: #000;
	}
	.item-titless{
		position: absolute;
		top: 0px;
	}
	.item-title111 {
		font-size: 28rpx;
		color: #000;
		 width: 35%;	
	
		// margin-right: 45rpx;
	}
	
	.item-title20{
		// width: 38%;
		// flex: 1 ;
		position: relative;
		
	}
	.item-title1 {
		margin-right: 0rpx;
	}

	.it1 {
		font-weight: 500;
		padding-left: 10rpx;
	}
	.it2 {
		padding-left: 10rpx;
		font-weight: 500;
		color: #2256d8;
	}
	.statusd {
		color: #888888;
	}
	.last-cild {
		margin-right: 0;
		text-align: right;
		width: 27%;
	}
	.it3 {
		padding-left: 10rpx;
		font-size: 28rpx;
		color: #000;
		width: 540rpx;
		font-weight: 500;
		overflow: hidden;
		word-break: break-all; /* break-all(允许在单词内换行。) */
		text-overflow: ellipsis; /* 超出部分省略号 */
		display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
		-webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
		-webkit-line-clamp: 2; /** 显示的行数 **/
	}
	.it33{
		margin-left: 100rpx;
		width: 150rpx;
		
		display: inline-block;
		
	}
	.items {
		margin-bottom: 20rpx;
		display: flex;
		flex-direction: row;
		justify-content: space-between; 
		
		
	}
	.it4 {
		
		justify-content: left;
	}

	.video-tip {
		position: absolute;
		left: 0;
		top: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.3);
	}
	.video-tip-icon {
		width: 60upx;
		height: 60upx;
	}
}
</style>
